import React from "react";
import Header from "../../components/header/Header";
import Slider from "../../components/slider/Slider";
import Resume from "../../components/resume/Resume";
import ContactInfo from "../../components/contact/ContactInfo";
import Footer from "../../components/footer/Footer";
import useDocumentTitle from "../../components/useDocumentTitle";
import About from "../../components/about/About";
import Results from "../../components/resume/Results";

const HomeOne = () => {
    useDocumentTitle("psychologist");
    document.body.classList.add("theme-light");
    return (
        <div className="main-left">
            <Header/>
            <Slider/>
            <Resume/>
            <Results/>
            <About/>

            <section id="contactus" className="section theme-light dark-bg">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-5 col-xl-4 m-15px-tb">
                            <ContactInfo/>
                        </div>
                        <div className="col-lg-7 ml-auto m-15px-tb  ">
                            <div className="contact-form">
                                <img style={{'border-radius': '15px'}}
                                     src="https://thumbs.dreamstime.com/b/%D1%81%D0%BB6%D0%B5%D0%BD%D1%89%D0%B8%D0%BD%D0%B0-pin-%D0%B2%D0%B2%D0%B5%D1%80%D1%85-%D0%BE%D1%82%D0%B2%D0%B5%D1%87%D0%B0%D0%B5%D1%82-%D1%82%D0%B5-%D0%B5%D1%84%D0%BE%D0%BD%D1%83-%D0%B8-%D1%8E%D1%81%D1%82%D1%80%D0%B0%D1%86%D0%B8%D1%8F-%D1%81%D1%82%D0%B8-%D1%8F-81031552.jpg"
                                     alt=""/>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <footer className="footer white">
                <div className="container">
                    <Footer/>
                </div>
            </footer>
        </div>
    );
};

export default HomeOne;
